import React, { useCallback, useState } from 'react';
import PropTypes from 'prop-types';
import { Icon } from 'antd';
import ImagesZoom from './ImagesZoom';


{/*이미지를 가지고와서 클라이언트에 뷰로 보여줄 수 있게 한다.
  이미지가 3개 이상이면 더보기 클릭해서 더 볼 수 있게해준다.*/}
const PostImages = ({ images }) => {
  const [showImagesZoom, setShowImagesZoom] = useState(false);

  const onZoom = useCallback(() => {
    setShowImagesZoom(true);
  }, []);

  const onClose = useCallback(() => {
    setShowImagesZoom(false);
  }, []);

  if (images.length === 1) {
    return (
      <>
        <img src={`http://49.247.193.211:3065/uploads/${images[0].src}`} onClick={onZoom} />
        {showImagesZoom && <ImagesZoom images={images} onClose={onClose} />}
      </>
    );
  }
  if (images.length === 2) {
    return (
      <>
        <div>
          <img src={`http://49.247.193.211:3065/uploads/${images[0].src}`} width="50%" onClick={onZoom} />
          <img src={`http://49.247.193.211:3065/uploads/${images[1].src}`} width="50%" onClick={onZoom} />
        </div>
        {showImagesZoom && <ImagesZoom images={images} onClose={onClose} />}
      </>
  );
  }
  return (
    <>
    <div>
      <img src={`http://49.247.193.211:3065/${images[0].src}`} width="50%" onClick={onZoom} />
      <div style={{ display: 'inline-block', width: '50%', textAlign: 'center', verticalAlign: 'middle' }} onClick={onZoom}>
        <Icon type="plus" />
        <br />
        {images.length - 1}
        개의 사진 더보기
      </div>
    </div>
      {showImagesZoom && <ImagesZoom images={images} onClose={onClose} />}
    </>
  );
};

PostImages.propTypes = {
  images: PropTypes.arrayOf(PropTypes.shape({
    src: PropTypes.string,
  })).isRequired,
};

export default PostImages;
